import React, { useEffect, useState } from 'react'
import './style.css'
import '../Common/HeaderComponent/style.css'

import * as backimg from '../../assets/images/Video/VID_bg.jpg'; 
import { useHistory } from 'react-router-dom';
import '../Common/Shareset/style.css'
 
import Spinner from 'components/Common/Spinner';
 
 
const VideoScreen = (props:any) => { 
  const [spinner, setspinner] = useState(true)

  const history = useHistory();  
  
  useEffect(() => {
    console.log("Video screeeeeeennnnn",props);
    setspinner(false) 
      }, [])

  return (
    <div>
      {spinner ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 1000,
          backgroundColor: '#00243a',
        }}>
          <Spinner />
        </div> :
        null}

      <div className='videoscreen_maindiv'>
        <img className={'video_backimg'}
          src={backimg}
        />

        
        <div className='videoscreen_maincontainer'>

          <div className='videoscreen_container'>
            <img
              onClick={() => {
                history.replace('/TaskListScreen')

              }}
              className='video_logo' src={props?.sl_icon} />
            <div className='video_logotextmain'>
              <div className='video_logotext1'>{props?.sl_title}</div>
              {/* <div className='video_logotext2'>Location: {list.Location}</div> */}
            </div>
            <div className="video_videoplayer">
              <div className="loader" />
              <iframe className="iframe" src={props?.sl_media} width="100%" height="100%" frameBorder="0"></iframe>
            </div>
          </div>

        </div>

      </div>
 

    </div>
  )
}

export default VideoScreen