import React, { useEffect, useState } from 'react'
import './style.css'
import QrReader from 'react-qr-reader'
import McInput from 'components/Common/McInput';
import { instancestore } from '../../store/InstanceStore';
import { useHistory } from 'react-router-dom';

import { useActiveScreen } from '../../store/ActiveScreen';
import { useAuth } from '../../store/authStore'
 
import { useParams } from 'react-router-dom';
import { userActiveLevelDetailsStore } from '../../store/ActiveLevelDetailsStore'
import { postGamedataLog } from "../../utils/actions"
import * as close_icon from '../../assets/close_icon.svg'


import { levelstore } from '../../store/levelStore';


const QRscanner = (props: any) => {


    const history = useHistory();

    const { instance, loadinstance } = instancestore();

    const [instanceid, setinstanceid] = useState('');

    const { setActiveScreen } = useActiveScreen();
    const { auth } = useAuth();

    const params = useParams();
 


    const { level, loadlevelwithid } = levelstore();

    const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();
    useEffect(() => {
        if (!instance) {
            loadinstance()
        }
        loadlevelwithid(params.instanceId)
    }, [])


    const findid = (id: any) => {
        let a: number = null;
        (level) && level.forEach((element: any) => {
            if (String(element.l_qr_id) === String(id)) {
                a = Number(element.id)
            }
        });
        return a;
    }
    const Instancefilter = () => {
        return (<div>
            <McInput
                type="picker"
                id="input_instanceidtextbox"
                name={`INSTANCE ID`}
                required={true}
                value={instanceid?.value}
                onchange={(data: any) => {
                    setinstanceid(data)
                    history.push(`/${data.key}/`)
                    window.location.reload();
                }}
                options={(instance) && instance.map(
                    (obj: any) => {
                        return { "key": obj.id, "value": obj.i_title }
                    }
                )}
            />
        </div>)
    }

    return (
        <div className='taskslist_scannerpopup_bg'>
            <img className="close_button" src={close_icon} onClick={() => {
                setActiveScreen((olddata) => {
                    let a = olddata.split("/")
                    return a[0]
                })
            }} />
            <div className='taskslist_scannerpopup_container'>
                <div className="Instancefilter">
                    <Instancefilter />
                </div>
                <div className='QRscannerText'>Scan the QR CODE to unlock the level!</div>

                <div className='taskslist_scannerpopup'>
                    <QrReader
                        onScan={(data: any) => {
                            if (data !== null) {
                                console.log(data);

                                let a = data.split("/")
                                let b = a[a.length - 2]
                                let id = String(b).substring(4, b.length)
                                let iid = a[a.length - 3]
                                console.log("The level id is ", id, "the instace id is", iid);
                                if (auth) {
                                    if (String(params.instanceId) === String(iid)) {
                                        loadActiveleveldetailsbyqr(id)
                                        setActiveScreen("TaskScreen")
                                        let data: any =
                                        {
                                            'level_sublevel_id': findid(id),
                                            'level_sublevel': "LEVEL", //LEVEL,SUBLEVEL
                                            'data': "dataasdaslkdfjjdf",
                                            'misc': "",
                                            'status': "ACTIVE" //LOCKED,ACTIVE,DONE
                                        }
                                        postGamedataLog((data: any, errorresponse: any) => {
                                            if (data.status === 200) {
                                                console.log(" game data logged", data.data.result.file_url)
                                            }
                                            else {
                                                console.log('error ' + JSON.stringify(data));
                                                console.log('error ' + JSON.stringify(errorresponse));
                                            }
                                        }, auth, data)
                                    }
                                    else
                                        if (String(params.instanceId) === "4") {
                                            window.location.href = data
                                            // window.location.reload()
                                        }
                                }
                            }
                        }
                        }
                        facingMode={"environment"}
                        onError={(data: any) => { console.log(data) }}
                    />
                </div>
            </div>
        </div>
    )
}

export default QRscanner
