import React, { useEffect } from 'react'
import './style.css'
import VideoScreen from 'components/VideoScreen'
import QuizScreen from 'components/QuizScreen'
import ActivityScreen from 'components/ActivityScreen' 
import { userActiveSubLevelDetailsStore } from '../../store/ActiveSubLevelDetailsStore'


const SubLevelScreen = () => {

  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();
  useEffect(() => {
    if (!ActiveSubLevelDetails) {
      loadActiveSubleveldetails(localStorage.getItem("ActiveSubLevel"))
    }
  }, [])  
  return (
    <> 
      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "VIDEO" &&
        <VideoScreen {...ActiveSubLevelDetails} />}

      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "QUIZ" &&
        <QuizScreen {...ActiveSubLevelDetails} />}

      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "ACTIVITIES" &&
        <ActivityScreen {...ActiveSubLevelDetails} />}
 
    </>
  )
}
export default SubLevelScreen;