import React, { useEffect, useState } from 'react';
import './style.css';
import * as backimgtop from '../../assets/Clean Walk/Levels_Screen/BG_top.svg';
import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import * as menu from '../../assets/Clean Walk/Video/VID_next.png'

import Spinner from 'components/Common/Spinner';
import HeaderComponent from 'components/Common/HeaderComponent';
import * as close from '../../assets/Clean Walk/close.png'


const QuizScreen = (props: any) => {
  const [spinner, setspinner] = useState(true)
  const [submitactivate, setsubmitactivate] = useState(false)
  const [submitactivate1, setsubmitactivate1] = useState(false)

  const [popup, setpopup] = useState(false)


  const [activatecontinue, setactivatecontinue] = useState(false)



  const [inputtext, setinputtext] = useState('')
  const [list, setlist] = useState({
    "Quiz": "1",
    "QuestionType": "Options",
    "Title": "CHALLENGE",
    "Question": "What was the last place you walked to?",
    "subHeading": "Did You Know?",
    "FunFact": "International guidelines recommend about 7000 steps a day for good physical and mental well being.",
    "source": "null",
    "Optiondata": [
      {
        "Option": "1",
        "Answer": "The grocery store",
        "active": "true"
      },
      {
        "Option": "2",
        "Answer": "The parking lot",
        "active": "false"

      },
      {
        "Option": "3",
        "Answer": "The bathroom",
        "active": "false"

      },
      {
        "Option": "4",
        "Answer": "Churchstreet",
        "active": "false"

      }
    ]
  })

  let params = useParams();

  const history = useHistory();

  useEffect(() => {
    setspinner(false)
    console.log(props);
  }, [])


  const getoptionlist = (options: String) => {
    console.log("======", options);
    let arr = options.split("/")
    console.log("===arr===", arr);
    return (
      arr.map((opt: any) => {
        return <div className="option_button_Orange">{opt}</div>
      })
    )
  }

  return (

    <div style={{ backgroundColor: "#f1fdfd" }}>
      {popup ?
        <>
          <div className="popup_bg">
            <div className="popup_container">
              <div className="popup_closecontainer">
                <img onClick={() => { setpopup(false) }} className='quiz_close' src={close} />
              </div>
              <div className="popup">

                <div className='PageTitle_OrangeU'>{list.subHeading} </div>
                <div className='popup_Text'>{list.FunFact}</div>
                {list.source !== "null" ? <div
                  onClick={() => {
                    window.open(list.source)
                  }} className='popup_source'>
                  source
                </div> : null}

              </div>
              <div className='popup_continue_buttonContainer'>
                <button onClick={() => {

                  history.replace('/RewardScreen/' + params.id)
                }} className="continue_button_Orange">
                  <div className="button_text">Continue</div>
                </button>
              </div>
            </div>
          </div>
        </> : null
      }

      {spinner ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 1000,
          backgroundColor: '#00243a',
        }}>
          <Spinner />
        </div> :
        null}
      <span>
        <img className='bgimgtop'
          src={backimgtop}
        />

      </span>


      <div className="quiz_shareposition"
        onClick={() => {
          history.replace('/TaskScreen/' + params.id)
        }} >
        <img className='quiz_menuicon' src={menu} />
      </div>
      <HeaderComponent />


      <div className="quiz_containerebottom">

        {(props.sl_bodytype === "OPTIONS") ?
          <>
            <div className="quiz_container">
              <div className='PageTitle_OrangeU'>{props.sl_title}</div>
              <div className='pageDescription_Text'>{props.sl_body}</div>
            </div>

            <div className="quiz_actioncontainer">
              {getoptionlist(props.sl_bodyoptions)}
            </div>
          </>
          :
          <>
            <div className="quiz_container">
              <div className='PageTitle_OrangeU'>{props.sl_title}</div>
              <div className='pageDescription_Text'>{props.sl_body}</div>
            </div>

            <div className="quiz_actioncontainer">
              <input id="inputbox" placeholder={inputtext} className='quiz_input' type="text" />
            </div>

            <div className="quiz_actioncontainer">
              {submitactivate ?
                <button onClick={() => {

                }} className="continue_button_Green">
                  <div className="button_text">Continue</div>
                </button>
                :
                <button onClick={() => {
                  setsubmitactivate(!submitactivate)

                }} className="continue_button_Orange">
                  <div className="button_text">SUBMIT</div>
                </button>
              }
            </div>

          </>
        }


        <div className="quiz_submit">

          {submitactivate1 ?
            <>
              {
                !activatecontinue ?
                  <button onClick={() => {
                    setactivatecontinue(!activatecontinue)
                    setpopup(true)
                    setsubmitactivate1(!submitactivate1)
                  }} className="continue_button_Orange">
                    <div className="button_text">SUBMIT</div>
                  </button>
                  :
                  <button onClick={() => {

                    history.replace('/RewardScreen/' + params.id)
                  }} className="continue_button_Green">
                    <div className="button_text">Continue</div>
                  </button>
              }
            </>

            : null
          }
        </div>

      </div>
      <div className="footer" />
    </div >

  )
}

export default QuizScreen