import React, { useEffect, useState } from 'react'
import './style.css'
import * as backimgtop from '../../assets/Clean Walk/Levels_Screen/BG_top.svg';

import * as scanback from '../../assets/Clean Walk/Levels_Screen/LS_qr_base.png'
import * as lsqr from '../../assets/images/Levels_Screen/LS_qr.png'
import * as lockimg from '../../assets/lock.svg'

import { useHistory } from 'react-router-dom'
import HeaderComponent from 'components/Common/HeaderComponent';

import { levelstore } from '../../store/levelStore';
import { instancestore } from '../../store/InstanceStore';

import { useParams } from 'react-router-dom';
import { useAuth } from '../../store/authStore';

import { leveldetailslstore } from '../../store/leveldetailsStore';
import { useActiveScreen } from '../../store/ActiveScreen';
import { userActiveLevelDetailsStore } from '../../store/ActiveLevelDetailsStore';


import { useLocation } from "react-router-dom";
import is from 'date-fns/esm/locale/is/index.js';

const TaskListScreen = (props: any) => {
  const { level, loadlevelwithid } = levelstore();
  const { instance, loadinstance } = instancestore();
  const { loadActiveleveldetails } = userActiveLevelDetailsStore();

  const { auth } = useAuth()
  const history = useHistory();
  const [spinner, setspinner] = useState(true)

  const { setActiveScreen } = useActiveScreen();
  const { loadleveldetails } = leveldetailslstore();

  const navigate = (object: any) => {
    console.log(object);
    setActiveScreen("TaskScreen")
    loadleveldetails()
    loadActiveleveldetails(object.id)
    localStorage.setItem("ActiveLevel", object.id)
  }
  let params = useParams();

  const search = useLocation().search;

  const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();
  useEffect(() => {
    if (!auth) {
      history.replace("/")

    }
    if (!instance) {
      loadinstance()
    }

    if (params.instanceId) {
      loadlevelwithid(params.instanceId)
    }
    else {
      if (props.id) {
        loadlevelwithid(props.id)
      }
    }

    const id = new URLSearchParams(search).get('id');
    if (id) {
      console.log("======>>>", id)
      loadActiveleveldetailsbyqr(id)
      setActiveScreen("TaskScreen")
    }
    setspinner(false)


  }, [])

  const loadinstanceicon = (instanceid: any) => {
    let a = " ";
    (instance) && instance.map((obj: any) => {

      if (String(instanceid) === String(obj.id)) {
        a = obj.mascot
      } else { null }
    })
    return a
  }
  const islocked = () => {
    // To do need to add logic to checck all the levels of unlock after are unlocaked 
    return false
  }
  const RenderList = (obj: any) => {
    return (<div className={'level' + ' ' + `id${obj.l_level}`} onClick={() => {
      if (!obj.is_locked) {
        if (!obj.GameDataLog[0]) {
          setActiveScreen((olddata) => {
            return `${olddata}/ActiveQR`
          })
        }
        else {
          navigate(obj)
        }
      }
    }}>
      <div className="icons">
        <div className='level_clue_image_wrapper shadow'>
          <img className='level_clue_image' src={obj.clue_image} />
        </div>
        <div className='leveltype_wrapper shadow'>
          <img className='leveltype' src={obj.l_icon} />
        </div>
        {obj.is_locked?
          <div className='level_status_wrapper shadow'>
            <img className='levelstatus' src={lockimg} />
          </div>
          : !obj.GameDataLog[0] ?
            <div className='level_status_wrapper shadow'>
              <img className='levelstatus_qr' src={lsqr} />
            </div>
            :
            null
        }
      </div>
      <div className="title">{obj.l_title}</div>

    </div>
    )
  }

  return (
    <div className="back_color_main">

      <HeaderComponent />

      <div className='levels'>
        <div className='instance_mascot_wrapper'>
          <img className='instance_mascot' src={loadinstanceicon(params.instanceId)} />
        </div>
        {
          (level) && level.sort((obj1: any, obj2: any) => obj1.l_level - obj2.l_level).map(RenderList)
        }
      </div>

      <div className="footer">
        <div className='footer_bg_circle'>
        </div>
        <div
          onClick={() => {
            setActiveScreen("TaskListScreen/ActiveQR")
          }}
          className='task_scanqrcontainer'>
          <img className='task_scanqr' src={lsqr} />
        </div>
      </div>

    </div>
  )
}


export default TaskListScreen
